import React from 'react';
import * as CSS from './css';
import eyeClose from '../../Static/eyeClose.svg';
import eyeOpen from '../../Static/eyeOpen.svg';
import * as COM from './Components/';

const SignIn = (props)=>(
    <CSS.AuthCard>
        <CSS.FormHeader>
            Hello World
        </CSS.FormHeader>
        <CSS.InputMask>
            <COM.InputWithErr 
                id="id"
                verification={props.verification.phone}
                input={{
                    placeholder: "手机号或邮箱",
                    value: props.state.User.id,
                    onChange: props.InputCtrl.InputHandle,
                    autoFocus: true
                }}
                err={{
                    placeholder: "手机号码或邮箱格式错误"
                }}
            />
        </CSS.InputMask>
        <CSS.InputMask>
            <COM.InputWithErr 
                id="pwd"
                input={{
                    placeholder: "密码",
                    type: props.state.eyeOn ? "text" : "password",
                    value: props.state.User.pwd,
                    onChange: props.InputCtrl.InputHandle
                }}
                err={{}}
            />
            <button onClick={props.ClickHandle}>
                <img id="message" src={props.state.eyeOn? eyeOpen : eyeClose} alt="Eye"></img>
            </button>
        </CSS.InputMask>
        <CSS.Options>
            <button id="phoneLogin" onClick={props.ClickHandle}>手机验证码登录</button>
            <button id="lost" onClick={props.ClickHandle}>忘记密码？</button>
        </CSS.Options>
        <CSS.Submit id="submit" onClick={props.ClickHandle}>登录</CSS.Submit>
        <CSS.OtherOption>
            {
                props.OtherOption()
            }
        </CSS.OtherOption>
        <CSS.Switch>
            {
                props.Switch()
            }
        </CSS.Switch>
    </CSS.AuthCard>
)

export default SignIn;